    onClickCheckgrid_one(number) {
        console.log(number)
        const { PickerDate } = this.props
        const ST = moment(PickerDate).valueOf() + (31 * 60 * 60 * 1000) - (24 * 60 * 60 * 1000)
        const endtime = ST + (number * 60 * 60 * 1000)
        const starttime = endtime - 60 * 60 * 1000
        const start = moment(starttime).format("YYYYMMDDHHmmss")
        const end = moment(endtime).format("YYYYMMDDHHmmss")
        const Phistnum = this.props.caseinfo.HISID.toString().trim()
        this.setState({ check_state_one: number })
        this.getVitalSignData(start, end, Phistnum)
    }
